/* src/CountdownBoard.css */

.countdown-board {
    text-align: center;
    margin: 20px auto;
    padding: 20px;
    /* background-color:#5e9a8d; */
    background-color: #1e7b67;
    border: 2px solid #ddd;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    max-width: 100%;
  }
  
  h2 {
    font-size: 24px;
    color: #333;
    margin-bottom: 20px;
  }
  
  .countdown-timer {
    display: flex;
    justify-content: space-between;
  }
  
  .timer-element {
    flex: 1;
    text-align: center;
    font-size: 20px;
    color: #333;
    border: 2px solid #ddd;
    border-radius: 5px;
    padding: 5px;
    margin: 0 5px;
  }
  
  .timer-element span:first-child {
    font-weight: bold;
    font-size: 36px;
    display: block;
  }


/* BlastComponent.css */
.blast {
  animation: confettiBlast 1s ease-out infinite;
}


  /* BlastAnimation.css */
@keyframes blastAnimation {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(2);
    opacity: 0.5;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}


/* PartyPopperAnimation.css */
@keyframes partyPopperBlast {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  50% {
    transform: scale(1.2);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}

/* PartyPopperConfettiAnimation.css */
@keyframes confettiBlast {
  0% {
    transform: translateY(-100vh) rotate(0deg);
    opacity: 0;
  }
  100% {
    transform: translateY(100vh) rotate(360deg);
    opacity: 1;
  }
}


  